<template>
  <div id="app">
    <div id="map" style="width: 100%; height: 500px"></div>
    <button @click="importMap">استيراد الخريطة</button>
    <button @click="exportMap">تصدير الخريطة</button>
  </div>
</template>
<script>
import MindElixir from "mind-elixir";

export default {
  name: "App",
  data() {
    return {
      ME: null,
    };
  },
  mounted() {
    this.ME = new MindElixir({
      el: "#map",
      direction: MindElixir.LEFT,
      draggable: true,
      contextMenu: true,
      toolBar: true,
      nodeMenu: true,
      keypress: true,
    });
    this.ME.init(MindElixir.new("new topic"));
  },
  methods: {
    importMap() {
      // إنشاء عنصر input لاختيار ملف JSON
      const input = document.createElement("input");
      input.type = "file";
      input.accept = ".json";
      input.addEventListener("change", (event) => {
        const file = event.target.files[0];
        const reader = new FileReader();

        reader.onload = (e) => {
          try {
            const importedData = JSON.parse(e.target.result);
            this.ME.init(importedData);
          } catch (error) {
            console.error("حدث خطأ أثناء استيراد الملف:", error);
          }
        };

        reader.readAsText(file);
      });

      input.click();
    },
    exportMap() {
      const mapData = this.ME.getData();
      const jsonData = JSON.stringify(mapData);
      const blob = new Blob([jsonData], { type: "application/json" });
      const url = URL.createObjectURL(blob);

      const a = document.createElement("a");
      a.href = url;
      a.download = "mind_map.json";
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
  },
};
</script>
